.base-container {

    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    .header {
        
        font-size: 24px;
        font-family: "Open Sans", sans-serif;

    }
    .content {
        
        display: flex;
        flex-direction: column;

        .image {
            width: 21em;
            max-height: 200px;
            img {
                width: 100%;
                max-height: 200px;
            }
            .register-image {
                margin-top: 20px;
                object-fit: cover;
                object-position: 0 -80px;
            }
        }

        .form {
            margin-top: 2em;
            display: flex;
            flex-direction: column;
            align-items: center;

            .form-group {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                width: fit-content;

                label {
                    font-size: 20px;
                    font-family: "Open Sans", sans-serif;
                }

                input {
                    margin-top: 6px;
                    width: 100%;
                    height: 35px;
                    padding: 0px 10px;
                    font-size: 16px;
                    font-family: "Open Sans", sans-serif;
                    background-color: #f3f3f3;
                    border: 0;
                    margin-bottom: 20px;
                    border-radius: 4px;
                    transition: all 250ms ease-in-out;
                    
                    &:focus {
                        outline: none;
                        box-shadow: 0px 0px 12px 0.8px #0e56ce96;
                    }
                }
            }
        }
    }
    .footer {
        margin-bottom: 2em;
    }
}


//Login Main Container
.login {
    width: 27em;
    max-width: 95%;
    height: 40em;
    //min-height: 44em;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 5%;
    position: relative;
    z-index: 99;
    .container {
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #fff;
      box-shadow: 0px 0px 12px 2px rgba(15, 15, 15, 0.2);
      border-radius: 4px;
      position: relative;
      z-index: 99;
      width: 100%;
      height: 100%;
      z-index: 99;
      padding: 17px 10px;
      //transition: transform 200ms ease-in-out;
    }
    .right-side {
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 90%;
      background-color: #3498db;
      width: 100%;
      position: absolute;
      right: -34%;
      border-radius: 6px;
      z-index: 1;
      transition: all 400ms ease-in-out;
      cursor: pointer;
      box-shadow: 0px 0px 12px 2px rgba(15, 15, 15, 0.281);
  
      &.right {
        right: -40%;
        align-items: flex-end;
        &:hover {
          right: -45%;
        }
      }
      &.left {
        right: 40%;
        align-items: flex-start;
        &:hover {
          right: 45%;
        }
      }
  
      .text {
        font-size: 21px;
        font-weight: 500;
        color: #fff;
        margin-right: 3em;
        margin-left: 3em;
      }
    }
  }
  
  //Button
  .btn {
    font-size: 21px;
    padding: 5px 20px;
    border: 0;
    background-color: #3498db;
    color: #fff;
    border-radius: 3px;
    margin-top: 10px;
    transition: all 250ms ease-in-out;
    cursor: pointer;
    &:hover {
      background-color: #2386c8;
    }
    &:focus {
      outline: none;
    }
  }
  
  a {
    text-decoration: none;
  }
