:root {
    --primary: #fff;
}

.btn {
    padding: 8px 20px;
    border-radius: 4px;
    outline: none;
    border: none;
    cursor: pointer;
    white-space: nowrap;
    text-align: center;
}

.btn--primary {
    background-color: var(--primary);
    color: #242424;
    border: 1px solid var(--primary);
}

.btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--medium {
    padding: 8px 20px;
    font-size: 18px;
}

.btn--large {
    padding: 12px 26px;
    font-size: 20px;
}

.btn--mobile {
    text-align: center;
    border-radius: 4px;
    width: 100%;
    text-decoration: none;
    font-size: 24px;
    background-color: transparent;
    color: #fff;
    padding: 14px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
}

.btn--wide {
    padding: 12px 64px;
    font-size: 20px;
}

.btn--medium:hover, .btn--large:hover, .btn--mobile:hover {
    background: #fff;
    color: #242424;
    transition: all 0.3s ease-out;
}

.btn--wide:hover {
    color: #fff;
    background-color: #09f09b;
    transition: all 0.3s ease-out;
}

.blue {
    background-color: #27a6fb;
    color: #fff;
    border: none;
} 
.blue:hover { 
    background-color: #fff;
    color: #27a6fb;
}
.red {
    background-color: #fb274e;
    color: #fff;
    border: none;
}
.red:hover {
    background-color: #fff;
    color: #fb274e;
}
.green {
    background-color: #09f09b;
    color: #fff;
    border: none;
}
.green:hover {
    background-color: #fff;
    color: #09f09b;
}
.primary {
    background-color: #242424;
    color: #fff;
    border: none;
}
.primary:hover {
    background-color: #fff;
    color: #242424;
}

.btn-link {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    padding: 8px 16px;
    height: 100%;
    width: 100%;
}